footer {
  margin-top: 140px;
  height: 250px;
  background: #3b82f6;
  color: #fff;
}

footer .wrap {
  display: flex;
  justify-content: space-between;
  height: 100%;
  gap: 30px;
}

.footer-left {
  width: 30%;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.footer-left h4 {
  margin-bottom: 10px;
}
.footer-middle {
  width: 30%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: column;
}

.footer-middle span {
  font-size: 12px;
}

.footer-middle img {
  filter: brightness(0) invert(1);
}

.footer-right {
  width: 30%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  gap: 20px;
}

.contact-data {
  text-align: center;
}

.contact-data span {
  color: #fff;
  cursor: pointer;
  margin-left: 5px;
}

@media (max-width: 1440px) {
  .footer-left span {
    font-size: 14px;
    max-width: 250px;
  }

  .footer-right {
    align-items: flex-start;
  }

  .contact-data span {
    font-size: 12px;
  }

  .contact-data img {
    width: 16px;
    height: 16px;
  }
}

@media (max-width: 1024px) {
  .footer-middle span {
    font-size: 8px;
  }
}

@media (max-width: 768px) {
  footer .wrap {
    flex-direction: column;
    align-items: center;
    gap: 20px;
  }

  footer {
    height: auto;
  }

  .footer-left {
    width: 100%;
    text-align: center;
  }

  .footer-left span {
    max-width: 90%;
    margin: 0 auto;
  }

  .footer-middle {
    order: -1;
    margin-bottom: 20px;
  }

  .footer-middle img {
    width: 250px;
    height: auto;
  }

  .footer-right {
    width: 100%;
    align-items: center;
    flex-direction: row;
    margin: 20px 0;
  }

  .contact-data {
    display: flex;
    align-items: center;
    text-align: center;
    gap: 8px;
    flex-direction: column-reverse;
  }

  .contact-data span {
    font-size: 8px;
  }
}

@media (max-width: 375px) {
  .footer-right {
    flex-direction: column;
  }
  .contact-data {
    flex-direction: row;
  }
}
